<template>
    <div>
        <el-button type="primary" class="add">
            <router-link to="/Add" tag="span">添加</router-link>
        </el-button>
        <el-upload
                class="upload-demo"
                ref="upload"
                name="files"
                accept="application/vnd.ms-excel,application/x-xls,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                action="/member/upload"
                :on-success="success"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
        <el-table
                v-loading="loading"
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="number"
                    label="工号"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="姓名"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label="联系方式">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name:"Info",
        data() {
            return {
                tableData: [],
                fileList: [],
                loading: true
            }
        },
        created(){
            var that=this;
            fetch("/member/select").then(function (e) {
                return e.json();
            }).then(function (e) {
                that.tableData=e
                that.loading=false;
            })
        },
        methods:{
            success(response){
                if(response=="ok"){
                    var that=this;
                    fetch("/member/select").then(function (e) {
                        return e.json();
                    }).then(function (e) {
                        that.tableData=e
                        that.loading=false;
                    })
                }
            },
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleEdit(index, row) {
                this.$router.push({path:"/edit",query:row})
            },
            handleDelete(index, row) {
                var that=this;
                fetch("/member/del?mid="+row.mid).then(function (e) {
                    return e.text();
                }).then(function (e) {
                    if(e=="ok"){
                        that.tableData.splice(index,1);
                    }
                })
            }
        }
    }
</script>

<style scoped>
.add{
    position: absolute;top:10px;left: 0;
}
</style>


