<template>
    <el-container style="height:100vh">
        <el-header>Header</el-header>
        <el-container>
            <el-aside width="200px">
                <MyMenu></MyMenu>
            </el-aside>
            <el-main>
                <router-view style="position: relative"></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import MyMenu from "@/components/MyMenu"
    export default {
        name:"Main",
        components:{MyMenu}
    }
</script>

<style scoped>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #545c64;
        color: #333;
        text-align: center;
        line-height: 200px;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        line-height: 30px;
    }

    body > .el-container {
        margin-bottom: 40px;
    }

    .el-container:nth-child(5) .el-aside,
    .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }

    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }
</style>


