<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm" >
        <el-form-item label="工号" prop="number">
            <el-input type="text" v-model="ruleForm2.number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
            <el-input type="text" v-model="ruleForm2.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="phone">
            <el-input type="text" v-model="ruleForm2.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
            <el-button @click="resetForm('ruleForm2')">重置</el-button>
            <el-button @click="back">返回</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    export default {
        name:"Login",
        data() {
            var validateNumber = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入工号'));
                }else{
                    callback()
                }
            };
            var validateName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入用户名'));
                }else{
                    callback()
                }
            };
            var validatePhone = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入联系方式'));
                }else{
                    callback()
                }
            };
            return {
                ruleForm2: {
                    number: "",
                    name:"",
                    phone:"",
                    mid:""
                },
                rules2: {
                    name: [
                        { validator: validateName, trigger: 'blur' }
                    ],
                    Number: [
                        { validator: validateNumber, trigger: 'blur' }
                    ],
                    Phone: [
                        { validator: validatePhone, trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    var that=this;
                    if (valid) {
                        fetch("/member/update",{
                            method:"post",
                            headers:{"content-Type":"application/x-www-form-urlencoded"},
                            body:"name="+this.ruleForm2.name+"&number="+this.ruleForm2.number+"&phone="+this.ruleForm2.phone+"&mid="+this.ruleForm2.mid
                        }).then(function (e) {
                            return e.text();
                        }).then(function (e) {
                            if(e=="ok"){
                                that.$message({
                                    message:"修改成功",
                                    type:"success",
                                    duration:1000
                                })
                                that.$router.push("/member");
                            }else{
                                that.$message({
                                    message:"修改失败",
                                    type:"error",
                                    duration:1000
                                })
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            back(){
                this.$router.go(-1);
            }
        },
        created(){
            this.ruleForm2.name=this.$route.query.name;
            this.ruleForm2.phone=this.$route.query.phone;
            this.ruleForm2.number=this.$route.query.number;
            this.ruleForm2.mid=this.$route.query.mid;
        }
    }
</script>
<form action="" enctype="application/x-www-form-urlencoded"></form>
<style scoped>
    .demo-ruleForm{
        width: 40%;
        height: 250px;
        position: absolute;top:0;left: 0;right:0;bottom: 0;
        margin: auto;
    }
</style>


