<template>
    <div>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="title"
                    label="标题"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="conq"
                    label="内容">
            </el-table-column>
        </el-table>
        <el-button-group>
            <el-button type="primary" icon="el-icon-arrow-left" @click="pre">上一页</el-button>
            <el-button type="primary" @click="next">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
        </el-button-group>
    </div>
</template>

<script>
    export default {
        name:"News",
        data() {
            return {
                tableData: [],
                page:0,
                flag:true
            }
        },
        created(){
            var that=this;
            fetch("/news/select?page="+this.page).then(function (e) {
                return e.json()
            }).then(function (e) {
                if(!e.message){
                    that.tableData=e;
                }
            })
        },
        methods:{
            pre(){
                console.log(this.page)

                this.page=this.page<=0?0:this.page-1;
                var that=this;
                fetch("/news/select?page="+this.page).then(function (e) {
                    return e.json()
                }).then(function (e) {
                    if(!e.message){
                        that.tableData=e;
                        that.flag=true;
                    }
                })
            },
            next(){
                if(this.flag){
                    this.page++;
                    var that=this;
                    fetch("/news/select?page="+this.page).then(function (e) {
                        return e.json()
                    }).then(function (e) {
                        if(e.message){
                            that.flag=false;
                            that.page--;
                        }else{
                            that.tableData=e;
                        }
                    })
                }

            }
        }
    }
</script>

<style scoped>

</style>


